.btn {
  text-transform: uppercase;
  padding:1.5rem 4rem;
  background-color:$color-primary;
  color: $color-Vdark-blue;
  font-size:1.2rem;
  letter-spacing: 3px;
  font-weight: 700; 
  border: none;
  border-radius: 1px;
  transition:all .3s ;
  cursor: pointer;

  &:hover {
    background-color:$color-Vdark-blue;
    color:#fff;
  }
}