$color-primary: hsl(5, 85%, 63%);
$color-secondary: hsl(35, 77%, 62%);
$color-grey-light-1:gray;
$color-white: hsl(36, 100%, 99%);
$color-light-blue: hsl(233, 8%, 79%);
$color-Dark-blue: hsl(236, 13%, 42%);
$color-Vdark-blue: hsl(240, 100%, 5%);

$bp-largest:93.75em;
$bp-large:75em;
$bp-medium:56.25em;
$bp-small:37.5em;
// $bp-large:87.75em;

html {
  font-size:62.5%;
  font-family:sans-serif;
  font-weight: 400;
 
   @media only screen  and (max-width: $bp-large) {
    font-size: 50%; 
   }
}

body {
  box-sizing: border-box;
  width: 90vw;
  margin: 7rem auto;

    @media only screen  and (max-width: $bp-medium) {
     margin: 0rem ;
     width:100%;
   }


}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.container {
  display: grid;
  gap:3rem;
  box-shadow: 0 2rem 4rem rgba(#000,.2);
  padding:4rem 8rem;

  
 @media only screen  and (max-width: $bp-largest) {
    padding: 4rem 8rem;
  }

   @media only screen  and (max-width: $bp-large) {
  padding:1rem 2rem;
    }

  @media only screen  and (max-width: $bp-medium) {
    // padding:0;
    gap:1rem;
   }

       @media only screen  and (max-width: $bp-small) {
    padding:1rem;
   }
    

}


main {
  display: grid;
  grid-template-columns:repeat(3,1fr);
  column-gap:3rem;

  @media only screen  and (max-width: $bp-largest) {
    row-gap:4rem;
  }

    @media only screen  and (max-width: $bp-large) {
    row-gap:2rem;
  }
 
     @media only screen  and (max-width: $bp-small) {
      grid-template-columns: 1fr;
   }
}


.heading-1 {
  font-size: 5rem;
  grid-column: 1/2;
}

.heading-2 {
  color: $color-secondary;
  font-size: 3rem;
}

.heading-3 {
  font-size:1.6rem;
  color:#fff;
  transition: all .2s;
  margin-bottom: 1rem;
  cursor: pointer;

  &:hover {
    color: $color-secondary;
  }
}

.heading-4 {
  font-weight: 600;
  font-size:1.8rem;
  transition: all .2s;
  cursor: pointer;

  &:hover {
    color: $color-primary;
  }
}