.header {
  display: grid;
  grid-template-columns: subgrid;
  grid-column: 1/3;
  row-gap:3rem;

    @media only screen  and (max-width:$bp-medium) {
      grid-column: 1/-1;
   }

  &__img {
    width: 100%;
    grid-column:1/3;

         @media only screen  and (max-width: $bp-medium) {
      grid-column: 1/-1;
   }

  }


}

.info {
 display: flex;
 flex-direction: column;
 align-items: start ;
 gap:2rem;

       @media only screen  and (max-width: $bp-medium) {
       grid-column: 1/3;  
      //  padding:0 4rem;
       grid-column: 1/-1; 
       padding-bottom: 8rem;

   }



  &__text {
    color:$color-light-blue;
    font-size: 1.4rem;
    line-height: 2;
  }
}
