.features {
  display: grid;
  grid-template-columns:repeat(auto-fit, minmax(30rem, 1fr));
  grid-column: 1/-1;  
  gap:1rem;

  @media only screen  and (max-width:$bp-small) {
     padding:3rem;
   }

.description {
  display: grid;
  gap: 2rem;
  grid-template-columns: min-content 1fr;
  align-items:center ;

    @media only screen  and (max-width:$bp-small) {
   }

    &__box {
    display: grid;
    gap: 1rem; }

    &__text  {
  color:$color-light-blue;
  line-height: 1.5;
  font-size: 1.5rem; }

  &  img {
  height: 60%;
}

span {
  color:$color-primary;
  font-size: 3rem;
  font-weight: 600    ;
 }
}
}