.sidebar {
  background-color:$color-Vdark-blue;
  display: grid;
  padding:3rem;
  gap:5rem;
  grid-column: 3/-1;

     @media only screen  and (max-width:$bp-medium){
      grid-column: 1/-1;
   }
}

.details  {
  display: grid;
  gap:2rem;

     @media only screen  and (max-width: $bp-medium) {
    grid-template-columns: repeat(auto-fit,minmax(25rem,1fr));
   }

     @media only screen  and (max-width: $bp-small) {
       grid-template-columns:1fr;
   }



}

.news {
    & p {
   color:$color-Dark-blue; 
   font-size: 1.5rem;
   line-height:1.5;
   padding-bottom: 3rem;

   &::after {
     height:2px;
     color:white;
     width:10rem;
   }
  }
}