

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__list {
    width: 35%;
    list-style: none;
    display: flex ;
    justify-content: space-between;
    font-size: 1.2rem;
    color: $color-grey-light-1;
    cursor: pointer;

    @media only screen  and (max-width: $bp-medium) {
       width:50%;
   }

     @media only screen  and (max-width: $bp-small) {
       flex-direction: column;
       position: absolute;
       top:50vh;
       left:50vw;
       transform: translate(-50%,-50%);
       align-items: center;

   }
  }

  &__item {
    font-size:1.4rem;
    transition: all .2s;
    
    &:hover {
      color:$color-primary;
    }
  }
}

.icon {
  padding:0 2rem ;
}
